@import url('https://fonts.googleapis.com/css?family=ABeeZee:400,400i&display=swap');

.header {
  background-color: rgba(0, 0, 0, 0.2);
  height: 76px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-family: 'ABeeZee';
  font-size: 1em;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 3;

  img {
    width: auto;
    height: 3.1666em;
  }

  nav {
    align-self: center;

    ul {
      list-style: none;

      li {
        display: inline-block;
        text-transform: uppercase;
        margin-right: 6em;
        color: white;
        letter-spacing: 0.03em;

        .active {
          color: #F2C94C;
        }

        a {
          text-decoration: none;
          color: white;
        }

        &:last-child {
          margin-right: 0;
        }

        &:hover {
          cursor: pointer;
        }
      }
    }
  }
}
